class FloatingIPAPI {
  async getAllFloatingIPs(params) {
    const res = await client.get("/floating_ips", {
      params
    });
    if (Array.isArray(res.data.floating_ips)) {
      return res.data.floating_ips;
    }
    return [res.data.floating_ips];
  }
  async createFloatingIP(params) {
    const res = await client.post("/floating_ips", params);
    return res.data.floating_ip;
  }
  async deleteFloatingIP(id) {
    const obj = await this.getFloatingIP(id);
    if (obj.protection.delete) {
      return false;
    }
    await client.delete(`/floating_ips/${id}`);
    return true;
  }
  async getFloatingIP(id) {
    const res = await client.get(`/floating_ips/${id}`);
    return res.data.floating_ip;
  }
  async updateFloatingIP(id, params) {
    const currentData = await this.getFloatingIP(id);
    if ((!params.name || currentData.name == params.name) && (!params.description || currentData.description == params.description) && (!params.labels || currentData.labels == params.labels)) {
      logDebug("[FloatingIP] Nothing to update");
      return currentData;
    }
    const res = await client.put(
      `/floating_ips/${id}`,
      params
    );
    return res.data.floating_ip;
  }
  async changeProtection(id, params) {
    const res = await client.post(
      `/floating_ips/${id}/actions/change_protection`,
      params
    );
    return res.data.action;
  }
}

